.App {
  text-align: center;
}

.App-header {
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo {
  height: 22vmin;
  pointer-events: none;
}

.social-container {
  padding: 25px 50px;
}

a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-2px);
}

.white-icon {
  color: white;
}

.mailing-list-text {
  font-style: italic;
  font-weight: bold;
}

.mailing-list-container {
  padding-bottom: 2px;
}

.subscribe-btn {
  font-weight: bold;
  font-family: "Roboto";
  text-transform: uppercase;
}

.subscribe-btn:hover {
  color: grey;
  cursor: pointer;
}

.music-container {
  padding-top: 40px;
  transition: transform 250ms;
  display: inline-block;
}

.music-container:hover {
  transform: translateY(-2px);
}

.contact-text {
  padding-top: 10px;
  font-style: italic;
  font-size: 12px !important;
}

